// src/app/page.tsx

"use client";

import Image from 'next/image';
import { useLayoutEffect, useRef, useState, useEffect } from 'react';
import gsap from 'gsap';
import Countdown from '@/components/Countdown';
import HeroBackground from '@/components/HeroBackground';
import Preloader from '@/components/Preloader';

// Magnetic Button Component එක (වෙනසක් නැත)
const MagneticButton = ({ children, className = '' }: { children: React.ReactNode; className?: string }) => {
    const ref = useRef<HTMLButtonElement>(null);
    useEffect(() => {
        const el = ref.current;
        if (!el) return;
        const handleMouseMove = (e: MouseEvent) => {
            const { clientX, clientY } = e;
            const { left, top, width, height } = el.getBoundingClientRect();
            const x = clientX - (left + width / 2);
            const y = clientY - (top + height / 2);
            gsap.to(el, { x: x * 0.4, y: y * 0.4, duration: 1, ease: "elastic.out(1, 0.5)" });
        };
        const handleMouseLeave = () => { gsap.to(el, { x: 0, y: 0, duration: 1, ease: "elastic.out(1, 0.5)" }); };
        el.addEventListener('mousemove', handleMouseMove);
        el.addEventListener('mouseleave', handleMouseLeave);
        return () => {
            el.removeEventListener('mousemove', handleMouseMove);
            el.removeEventListener('mouseleave', handleMouseLeave);
        };
    }, []);
    const baseClasses = "px-6 py-3 sm:px-8 font-semibold rounded-lg transition-colors duration-300 ease-in-out";
    const combinedClasses = `${baseClasses} ${className}`;
    return <button ref={ref} className={combinedClasses} data-cursor-hover>{children}</button>;
};

export default function Home() {
  const [isLoading, setIsLoading] = useState(true);
  const comp = useRef(null);

  useLayoutEffect(() => {
    const ctx = gsap.context(() => {
      if (isLoading) {
        // --- අලුත් Logo Reveal Animation එක ---
        const tl = gsap.timeline({
            onComplete: () => setIsLoading(false)
        });

        // ලාංඡනය පහළ සිට උඩට මතුවී දර්ශනය වේ
        tl.from("#logo-container", {
            yPercent: 101, // 100% ක් පහළින් ආරම්භ වේ
            duration: 1.5,
            ease: 'power3.out',
            delay: 0.5
        })
        // තත්පර 1.5ක් ලාංඡනය පෙන්වා, පසුව උඩටම ගොස් මැකී යයි
        .to("#logo-container", {
            yPercent: -101, // 100% ක් උඩට ගොස් මැකී යයි
            duration: 1.5,
            ease: 'power3.in',
            delay: 1.5
        })
        // Preloader එක සම්පූර්ණයෙන්ම fade out වේ
        .to("#preloader", {
            opacity: 0,
            duration: 0.5
        }, "-=0.5"); // කලින් animation එක අවසන් වීමට මදක් පෙර

      } else {
        gsap.from("#welcome", { opacity: 0, duration: 1, delay: 0.2 });
      }
    }, comp);
    return () => ctx.revert();
  }, [isLoading]);

  return (
    <div className="bg-black font-orbitron" ref={comp}>
      {isLoading ? (
        <Preloader />
      ) : (
        <>
          <main id="welcome" className="relative flex flex-col items-center justify-center w-full h-screen bg-black overflow-hidden z-10">
            <div className="absolute top-0 left-0 w-full h-full z-0">
                <Image src="/hero-background.jpg" alt="Futuristic cyberpunk background" layout="fill" objectFit="cover" className="opacity-40" />
            </div>
            <HeroBackground />
            <div className="relative z-10 flex flex-col items-center text-center text-white p-5">
                <div className="w-full max-w-sm md:max-w-2xl mb-6">
                    <Image src="/nexentia-logo.png" alt="Nexentia '25 Logo" width={700} height={175} priority layout="responsive" />
                </div>
                <h1 className="text-lg sm:text-xl md:text-3xl font-bold tracking-wider uppercase mb-4 text-cyan-300 px-2">
                    FIRST EVER MINISTRY OF EDUCATION REGOGNIZED ICT DAY IN SRI LANKA
                </h1>
                <p className="text-base sm:text-lg md:text-xl font-light mb-10 text-gray-300 font-sans">
                    BY ST. JOSEPH'S GIRLS' SCHOOL
                </p>
                <div className="flex flex-col sm:flex-row gap-8">
                    <MagneticButton className="bg-transparent border-2 border-purple-500 text-purple-500 hover:bg-purple-500 hover:text-white shadow-[0_0_15px_rgba(168,85,247,0.5)]">
                        SCHOOL REGISTRATION
                    </MagneticButton>
                    <MagneticButton className="bg-purple-500 text-white hover:bg-purple-600 shadow-[0_0_15px_rgba(168,85,247,0.8)]">
                        EXPLORE COMPETITIONS!
                    </MagneticButton>
                </div>
            </div>
          </main>
          <Countdown />
        </>
      )}
    </div>
  );
}