// src/components/Preloader.tsx

import Image from 'next/image';

const Preloader = () => {
  return (
    // Preloader එකේ ප්‍රධාන container එක
    <div id="preloader" className="h-screen w-full fixed top-0 left-0 z-50 bg-black flex justify-center items-center">
      {/* ලාංඡනයට උඩින් ඇති ආවරණය (mask) ඉවත් වන විට ලාංඡනය පෙන්වීමට, overflow-hidden යොදා ඇත */}
      <div className="relative w-full max-w-md md:max-w-lg overflow-hidden">
        <div id="logo-container">
            <Image
              src="/nexentia-logo.png"
              alt="Nexentia '25 Logo"
              width={700}
              height={175}
              priority
              layout="responsive"
            />
        </div>
      </div>
    </div>
  );
};

export default Preloader;